<template>
    <div class="container">
        <div class="row">
            <div class="col-xs-12">
                <v-breadnav>咨询详情</v-breadnav>
            </div>
        </div>
        <div class="row">
            <div class="col-xs-12">
                <div class="story-desc" v-html="descUrl"></div>
            </div>
        </div>
    </div>
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
export default {
    data:function(){
        return{
            descUrl:this.$route.params.url
        }
    },
    components:{
        vBreadnav
    }
}
</script>



